import QSTabs from '../../components/QS-tabs/QS-tabs.vue';

import {
	mapState
} from 'vuex'
export default {
	// computed: mapState(['isLogin', 'mobile']),
	components: {
		QSTabs
	},
	data() {
		return {
			isLogin:false,
			title: 'Hello',
			br:'\n',
			tabs: ['动态','作品','文章'],
			current: 0,
			tabsHeight: 0,
			dx: 0,
			nickname:'Alan',
			face:''
		}
	},
	onShow: function() {},
	methods: {
		logout: function() {
			uni.showModal({
				title: '提示',
				content: '确定退出登录？',
				confirmColor: '#5677FC',
				success: (res) => {
					if (res.confirm) {
						uni.clearStorage()
						uni.reLaunch({
							url: '../login/login'
						})
					}
				}
			});
		},
		edit() {
			this.tui.toast("功能开发中~")
		},
		tapEvent: function(e) {
			let index = e.currentTarget.dataset.index;
			let url = "";
			if (index == 1) {
				url = '../about/about'
			} else if (index == 2) {
				let key = e.currentTarget.dataset.key;
				url = '../maps/maps?key=' + key




			} else {
				url = '../log/log'
			}
			uni.navigateTo({
				url: url
			})
		},
		github: function(type) {
			if (type == 1) {
				const that = this
				uni.setClipboardData({
					data: 'https://github.com/dingyong0214/ThorUI-uniapp',
					success(res) {
						uni.getClipboardData({
							success(res) {
								that.tui.toast("链接已复制", 2000, true)
							}
						})
					}
				})
			} else {



			}
		},
		previewReward: function() {
			uni.previewImage({
				urls: ["https://thorui.cn/img/reward.jpg"]
			})
		}
	}
}
